<template>
<div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
        <el-row>
            <el-col>
                <el-button type="primary" @click="addRolesDialogVisible = true"></el-button>
            </el-col>
        </el-row>
        <!---->
        <el-table :data="roleList" border stripe>
            <el-table-column type="expand">
                <template slot-scope="scope">
                    <el-row :class="['bdbottom',i1===0?'bdtop':'','vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                        <!---->
                        <el-col :span="5">
                            <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                            <i class="el-icon-caret-right"></i>
                        </el-col>
                        <el-col :span="19">
                            <!---->
                            <el-row :class="[i2===0?'':'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id">
                                <el-col :span="6">
                                    <el-tag type="success" closable @close="removeRightById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                                    <i class="el-icon-caret-right"></i>
                                </el-col>
                                <el-col :span="18">
                                    <el-tag type="warning" v-for="(item3,i3) in item2.children" :key="item3.id" closable @close="removeRightById(scope.row,item3.id)">
                                        {{item3.authName}}
                                    </el-tag>
                                </el-col>
                            </el-row>
                        </el-col>
                    </el-row>
                </template>
            </el-table-column>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="roleName"></el-table-column>
            <el-table-column label="" prop="roleDesc"></el-table-column>
            <el-table-column label="" width="300px">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)"></el-button>
                    <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeRolesById(scope.row.id)"></el-button>
                    <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </el-card>
    <!---->
    <el-dialog title="" :visible.sync="setRightDialogVisible" width="50%" @close="setRightDialogClosed">
        <el-tree :data="rightList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defkeys" ref="treeRef"> </el-tree>
        <span slot="footer" class="dialog-footer">
            <el-button @click="setRightDialogVisible = false"> </el-button>
            <el-button type="primary" @click="allotRights"> </el-button>
        </span>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="addRolesDialogVisible" width="50%" @close="addRolesDialogClose">
        <el-form :model="addRolesForm" :rules="addRolesFormRules" ref="addRolesFormRef" label-width="100px">
            <el-form-item label="" prop="roleName">
                <el-input v-model="addRolesForm.roleName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="roleDesc">
                <el-input v-model="addRolesForm.roleDesc"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addRolesDialogVisible  = false"> </el-button>
            <el-button type="primary" @click="addRoles"> </el-button>
        </span>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="editRolesDialogVisible" width="50%" @close="editRolesDialogClose">
        <el-form :model="editRolesForm" :rules="editRolesFormRules" ref="editRolesFormRef" label-width="100px">
            <el-form-item label="" prop="roleName">
                <el-input v-model="editRolesForm.roleName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="roleDesc">
                <el-input v-model="editRolesForm.roleDesc"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editRolesDialogVisible  = false"> </el-button>
            <el-button type="primary" @click="editRolesInfo"> </el-button>
        </span>
    </el-dialog>
</div>
</template>

<script>
export default {
    data() {
        return {
            // 
            roleList: [],
            setRightDialogVisible: false,
            // 
            rightList: [],
            // 
            treeProps: {
                label: 'authName',
                // children
                children: 'children'
            },
            // id
            defkeys: [],
            // id
            roleId: [],
            addRolesForm: {
                roleName: "",
                roleDesc: ""
            },
            addRolesDialogVisible: false,
            // 
            addRolesFormRules: {
                roleName: [{
                    required: true,
                    message: "",
                    trigger: "blur"
                }]
            },
            editRolesDialogVisible: false,
            editRolesForm: {
                roleName: "",
                roleDesc: ""
            },
            editRolesFormRules: {
                roleName: [{
                    required: true,
                    message: "",
                    trigger: "blur"
                }]
            },
            editRolesForm: {}
        }
    },
    created() {
        this.getRolesList()
    },
    methods: {
        async getRolesList() {
            const {
                data: res
            } = await this.$http.get('roles')
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.roleList = res.data
            console.log(this.roleList)
        },
        // id
        async removeRightById(role, rightId) {
            const confirmResult = await this.$confirm(
                ", ?",
                "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                }
            ).catch(err => err);
            if (confirmResult !== "confirm") {
                return this.$message.info("");
            }
            const {
                data: res
            } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
            if (res.meta.status !== 200) {
                return this.$message.error("");
            }
            this.$message.success("");
            role.children = res.data
        },
        // 
        async showSetRightDialog(role) {
            this.roleId = role.id
            const {
                data: res
            } = await this.$http.get('rights/tree')
            if (res.meta.status !== 200) {
                return this.$message.error("");
            }
            // 
            this.rightList = res.data
            console.log(this.rightList)
            this.getLeafKeys(role, this.defkeys)
            this.setRightDialogVisible = true
        },
        // id
        getLeafKeys(node, arr) {
            if (!node.children) {
                return arr.push(node.id)
            }
            node.children.forEach(item => this.getLeafKeys(item, arr))
        },
        setRightDialogClosed() {
            this.defkeys = []
        },
        //
        async allotRights() {
            const keys = [
                ...this.$refs.treeRef.getCheckedKeys(),
                ...this.$refs.treeRef.getHalfCheckedKeys()
            ]
            const idStr = keys.join(',')
            const {
                data: res
            } = await this.$http.post(`roles/${this.roleId}/rights`, {
                rids: idStr
            })
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.$message.success('')
            this.getRolesList()
            this.setRightDialogVisible = false
        },
        // id
        async removeRolesById(id) {
            const confirmResult = await this.$confirm(
                ", ?",
                "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                }
            ).catch(err => err);
            if (confirmResult !== "confirm") {
                return this.$message.info("");
            }
            const {
                data: res
            } = await this.$http.delete("/roles/" + id);
            if (res.meta.status !== 200) {
                return this.$message.error("");
            }
            this.$message.success("");
            this.getRolesList();
        },
        //
        addRoles() {
            this.$refs.addRolesFormRef.validate(async valid => {
                if (!valid) return;
                const {
                    data: res
                } = await this.$http.post("roles", this.addRolesForm);
                if (res.meta.status !== 201) {
                    this.$message.error("");
                }
                this.$message.success("");
                this.addRolesDialogVisible = false;
                this.getRolesList();
            });
        },
        addRolesDialogClose() {
            this.$refs.addRolesFormRef.resetFields();
        },
        //
        async showEditDialog(id) {
            const {
                data: res
            } = await this.$http.get("roles/" + id);
            if (res.meta.status !== 200) {
                return this.$message.error("");
            }
            this.editRolesForm = res.data;
            this.editRolesDialogVisible = true;
        },
        //
        editRolesInfo() {
            this.$refs.editRolesFormRef.validate(async valid => {
                if (!valid) return;
                const {
                    data: res
                } = await this.$http.put(
                    "roles/" + this.editRolesForm.roleId, {
                        roleName: this.editRolesForm.roleName,
                        roleDesc: this.editRolesForm.roleDesc
                    }
                );
                if (res.meta.status !== 200) {
                    return this.$message.error("");
                }
                this.editRolesDialogVisible = false;
                this.getRolesList();
                this.$message.success("");
            });
        },
        // editRolesDialogClose() {
        //     this.$refs.editRolesFormRef.resetFields();
        // },
    },
}
</script>

<style lang="less" scoped>
.bdtop {
    border-top: 1px solid #eee;
}

.bdbottom {
    border-bottom: 1px solid #eee;
}

.el-tag {
    margin: 7px;
}
</style>



