<template>
<div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
        <el-row>
            <el-col>
                <el-button type="primary" @click="showAddCateDialog"></el-button>
            </el-col>
        </el-row>
        <!---->
        <tree-table class="treeTable" :data="catelist" :columns="columns" :expand-type="false" :selection-type="false" show-index index-text="#" border :show-row-hover="false">
            <template slot="isok" slot-scope="scope">
                <i class="el-icon-success" v-if="scope.row.cat_deleted===false" style="color:lightgreen"></i>
                <i class="el-icon-error" v-else style="color:red"></i>
            </template>
            <template slot="order" slot-scope="scope">
                <el-tag size="mini" v-if="scope.row.cat_level===0"></el-tag>
                <el-tag type="success" size="mini" v-else-if="scope.row.cat_level===1"></el-tag>
                <el-tag type="warning" size="mini" v-else></el-tag>
            </template>
            <template slot="opt" slot-scope="scope">
                <el-button size="mini" type="primary" icon="el-icon-edit" @click="showCateEditDialog(scope.row.cat_id)"></el-button>
                <el-button size="mini" type="danger" icon="el-icon-delete" @click="removeCateById(scope.row.cat_id)"></el-button>
            </template>
        </tree-table>
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[3,5,10,15]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
    </el-card>
    <!---->
    <el-dialog title="" :visible.sync="addCateDialogVisible" width="50%" @close="addCateDialogClosed">
        <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
            <el-form-item label="" prop="cat_name">
                <el-input v-model="addCateForm.cat_name"></el-input>
            </el-form-item>
            <el-form-item label="">
                <!--options-->
                <!--cascaderProps-->
                <!--selectdKeysid-->
                <!--:props-->
                <!--checkStrictly: true -->
                <el-cascader v-model="selectdKeys" :options="parentCateList" :props="{expandTrigger:'hover',value: 'cat_id',label: 'cat_name', children: 'children',checkStrictly:'false'}" @change="parentCateChange" clearable></el-cascader>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addCateDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addCate"> </el-button>
        </span>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="editCateDialogVisible" width="50%">
        <el-form :model="editCateForm" :rules="editCateFormRules" ref="editCateFormRef" label-width="100px">
            <el-form-item label="" prop="cat_name">
                <el-input v-model="editCateForm.cat_name"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editCateDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editCateInfo"> </el-button>
        </span>
    </el-dialog>
</div>
</template>

<script>
export default {
    data() {
        return {
            // 
            queryInfo: {
                type: 3,
                pagenum: 1,
                pagesize: 5
            },
            // 
            catelist: [],
            // 
            total: 0,
            // table 
            columns: [{
                label: '',
                prop: 'cat_name'
            }, {
                label: '',
                // 
                type: 'template',
                // 
                template: 'isok'
            }, {
                label: '',
                // 
                type: 'template',
                // 
                template: 'order'
            }, {
                label: '',
                // 
                type: 'template',
                // 
                template: 'opt'
            }],
            addCateDialogVisible: false,
            // 
            addCateForm: {
                // 
                cat_name: '',
                // id
                cat_pid: 0,
                //
                cat_level: 0
            },
            // 
            addCateFormRules: {
                cat_name: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }]
            },
            //
            parentCateList: [],
            // 
            selectdKeys: [],
            editCateDialogVisible: false,
            editCateForm: {},
            editCateFormRules: {
                cat_name: [{
                    required: true,
                    message: "",
                    trigger: "blur"
                }]
            }
        };
    },
    created() {
        this.getCateList()
    },
    methods: {
        async getCateList() {
            const {
                data: res
            } = await this.$http.get('categories', {
                params: this.queryInfo
            })
            if (res.meta.status !== 200) {
                return this.$message.error("");
            }
            this.catelist = res.data.result
            this.total = res.data.total
            console.log(res.data)
        },
        // pagesize
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getCateList()
        },
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            this.getCateList()
        },
        showAddCateDialog() {
            this.getParentCateList()
            this.addCateDialogVisible = true
        },
        // 
        async getParentCateList() {
            const {
                data: res
            } = await this.$http.get('categories', {
                params: {
                    type: 2
                }
            })
            if (res.meta.status !== 200) {
                return this.$message.error("");
            }
            this.parentCateList = res.data
        },
        // 
        parentCateChange() {
            if (this.selectdKeys.length > 0) {
                // id
                this.addCateForm.cat_pid = this.selectdKeys[this.selectdKeys.length - 1]
                this.addCateForm.cat_level = this.selectdKeys.length
                return // 
            } else {
                this.addCateForm.cat_pid = 0
                this.addCateForm.cat_level = 0
            }
        },
        addCate() {
            //   
            this.$refs.addCateFormRef.validate(async valid => {
                if (!valid) return
                const {
                    data: res
                } = await this.$http.post('categories', this.addCateForm)
                if (res.meta.status !== 201) {
                    return this.$message.error("");
                }
                this.$message.success('')
                this.getCateList()
                this.addCateDialogVisible = false
            })
        },
        // 
        addCateDialogClosed() {
            this.$refs.addCateFormRef.resetFields()
            this.selectdKeys = []
            this.addCateForm.cat_pid = 0;
            this.addCateForm.cat_level = 0
        },
        async removeCateById(id) {
            const confirmResult = await this.$confirm(
                ", ?",
                "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                }
            ).catch(err => err);
            if (confirmResult !== "confirm") {
                return this.$message.info("");
            }
            const {
                data: res
            } = await this.$http.delete("/categories/" + id);
            if (res.meta.status !== 200) {
                return this.$message.error("");
            }
            this.$message.success("");
            this.getCateList()
        },
        async showCateEditDialog(id) {
            const {
                data: res
            } = await this.$http.get("categories/" + id);
            if (res.meta.status !== 200) {
                return this.$message.error("");
            }
            this.editCateForm = res.data;
            this.editCateDialogVisible = true;
        },
        editCateInfo() {
            this.$refs.editCateFormRef.validate(async valid => {
                if (!valid) return;
                const {
                    data: res
                } = await this.$http.put(
                    "categories/" + this.editCateForm.cat_id, {
                        cat_name: this.editCateForm.cat_name,
                    }
                );
                if (res.meta.status !== 200) {
                    return this.$message.error("");
                }
                this.editCateDialogVisible = false;
                this.getCateList()
                this.$message.success("");
            });
        },
    }
};
</script>

<style scoped>
.treeTable {
    margin-top: 15px;
}
</style>



